                                <!-- <img v-bind:src="card.player12" alt=""> -->
                                <img v-bind:src="player12" alt="">
                            </div>
                            <div class="player-info">
                                <div class="player-head">
                                    <img src="./../../../assets/图层15@2x.png">
                                </div>
                                <div class="player-name">
                                    <p class="player-nickname">nickname</p>
                                    <p>gold 2000</p>
                                </div>
                            </div>
                        </div>
                        <div class="player-box">
                            <div class="player-card">
                                <!-- <img v-bind:src="card.player20" alt=""> -->
                                <img v-bind:src="player20" alt="">                           
                                <!-- <img v-bind:src="card.player21" alt=""> -->
                                <img v-bind:src="player21" alt="">
                                <!-- <img v-bind:src="card.player22" alt=""> -->
                                <img v-bind:src="player22" alt="">
                            </div>
                            <div class="player-info">
                                <div class="player-head">
                                    <img src="./../../../assets/图层15@2x.png">
                                </div>
                                <div class="player-name">
                                    <p class="player-nickname">nickname</p>
                                    <p>金币 1999</p>
                                </div>
                            </div>
                        </div>
                        <div class="player-box">
                            <div class="player-card">
                                <!-- <img v-bind:src="card.player30" alt=""> -->
                                <img v-bind:src="player30" alt="">
                                <!-- <img v-bind:src="card.player31" alt=""> -->
                                <img v-bind:src="player31" alt="">
                                <!-- <img v-bind:src="card.player32" alt=""> -->
                                <img v-bind:src="player32" alt="">
                            </div>
                            <div class="player-info">
                                <div class="player-head">
                                    <img src="./../../../assets/图层15@2x.png">
                                </div>
                                <div class="player-name">
                                    <p class="player-nickname">nickname</p>
                                    <p>金币 1998</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="bets">
                        <div class="bets-title">庄对牌以上</div>
                        <div class="row col-name">
                            <div class="td">闲1</div>
                            <div class="td">闲2</div>
                            <div class="td">闲3</div>
                        </div>
                        <div class="row col-type">
                            <div class="td" value="player1" @click="bet($event)">
                                <p class="name" value="toDeal">对牌以上</p>
                            </div>
                            <div class="td" value="player2" @click="bet($event)">
                                <p class="name" value="toDeal">对牌以上</p>
                            </div>
                            <div class="td" value="player3" @click="bet($event)">
                                <p class="name" value="toDeal">对牌以上</p>
