                            </div>
                            <div class="td" value="player2" @click="bet($event)">
                                <p class="name" value="lose">输</p>
                                <p>1:{{desk.percent[12].odds}}</p>
                            </div>
                            <div class="td" value="player3" @click="bet($event)">
