                            <div class="td user-td">{{remaining}}</div>
                        </div>
                    </div>
                    <mt-button class="bets-btn" size="large" @click ="countDownf">下注</mt-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
console.log("GAME_SANGONG_SANGONG_VUE");

    export default {
        data(){
            return {
                desk:this.SANGONG.desk,
                remaining:this.GLOBAL.userInfo.gold,
                // card:this.SANGONG.card,
                banker0:this.SANGONG.card.banker0,
                player10:this.SANGONG.card.player10,
                player20:this.SANGONG.card.player20,
                player30:this.SANGONG.card.player30,

                banker1:this.SANGONG.card.banker1,
                player11:this.SANGONG.card.player11,
                player21:this.SANGONG.card.player21,
                player31:this.SANGONG.card.player31,

                banker2:this.SANGONG.card.banker2,
                player12:this.SANGONG.card.player12,
                player22:this.SANGONG.card.player22,
                player32:this.SANGONG.card.player32,

                banker3:this.SANGONG.card.banker3,
                player13:this.SANGONG.card.player13,
                player23:this.SANGONG.card.player23,
                player33:this.SANGONG.card.player33,

                countDown:this.SANGONG.countDown,//倒计时
           }
        },
        methods:{
            countDownf: function() {
                console.log("---------------countdown----------------");
                var bet = this.SANGONG.bet;
                console.log(bet);
                if( bet.length== 0){
                    console.log("-----no bet in-----");
                }  
                else{
                    //补充完整用户的信息
                    var info = this.betting();
                    this.SANGONG.socketHand.send(info);
                } 
            },
            bet: function(event){
                 var clickPlayer=event.currentTarget.getAttribute('value');//"player1"
                 var clickType=event.currentTarget.firstChild.getAttribute('value');//"sanGong"
                 if(event.currentTarget.style.background == 'red'){
                     event.currentTarget.style.background='';
                     this.remaining=this.delJson(clickPlayer,clickType);//刷新用户下注之后的金币
                 }
                 else{
                     event.currentTarget.style.background ='red';
                     this.remaining=this.addJson(clickPlayer,clickType);
                 }
            }   
        },
        mounted: function(){
            // this.$store.state.isPageLogin = true;
            document.getElementById('sangong').style.height = window.screen.height + 'px';
        },
        beforeCreate:function() {
               console.log(this.SANGONG.desk);
               var that = this;
               this.SANGONG.socketHand.onmessage = function(e){
                   console.log(e.data);
                   var data = JSON.parse(e.data);
                   switch(data.code){
                       //没有玩家押注时发牌渲染
                       case 4:
                       //console.log(data.deal);
                       that.SanGongDeal(that,data);
                       break;
                       //有玩家押注时返回的牌组渲染
                       case 6:
                       console.log("bet data read successfully");
                       break;
                       case 3:
                       that.SanGongBet(data);
                       break;
                       //有玩家押注时更新本地玩家金币
                       case 5:
                       that.SanGongGold( data );
                       default:
                       break;
                   }
                }
                document.getElementsByTagName("body")[0].className="mg0";
                document.getElementsByTagName("html")[0].className="mg0";
                // var timer = setInterval(function(){
                //     // console.log(that.GLOBAL.sanGong.countDown);
                //     if(that.SANGONG.desk.countDown>0){
                //         that.countDown = --that.SANGONG.desk.countDown;
                //     }
                //     if(that.countDown <= 0){
                //         clearInterval(timer);
                //         console.log("GAME Start");
                //     }
                // }, 1000);
        },

        beforeDestroy: function() {
            document.getElementsByTagName("html")[0].removeAttribute("class","mg0");
            document.body.removeAttribute("class","mg0");
        },

    }
    
</script>

<style scoped>
#sangong{
    background: url('./../../../assets/色彩平衡1@2x.png') no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    overflow-y: auto;
}
.game-container{
    padding: 10px;
}
.top{
    display: flex;
    justify-content: space-between;
}
.btn{
    display: flex;
    flex-wrap: nowrap;
}
.btn img{
    height: 35px;
    width: 35px;
    margin-left: 10px;
    margin-top: 2.5px;
}
.fapai img{
    height: 40px;
}
.fapai scan{
    color:white;
}
.banker{
    margin-top: 10px;
    padding: 0 30px;
    display: flex;
    flex-wrap: nowrap;
}
.banker-card{
    position: relative;
    height: 60px;
    width: 90px;
    padding-top: 10px;
}
.banker-card img{
    height: 60px;
    width: 40px;
    position: absolute;
}
.banker-card img:nth-child(2){
    left: 20px;
}
.banker-card img:nth-child(3){
    left: 40px;
}
.banker-info{
    margin-top: 2.5px;
    height: 60px;
    padding: 5px;
    border: 1px solid #AAA3E5;
    display: flex;
    flex-wrap: nowrap;
    border-radius: 5px;
}
.banker-head{
    height: 60px;
    width: 60px;
}
.banker-head img{
    height: 100%;
    width: 100%;
}
.banker-name{
    text-align: left;
    padding: 5px;
    font-size: 14px;
    color: #E7E3F6;
    line-height: 25px;
}
.banker-nickname{
    max-width: 70px;
    overflow: hidden;
}
.banker-name p:last-child{
    font-size: 16px;
}
.player{
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.player-box{
    width: 30%;
    min-width: 90px;
    overflow: hidden;
}
.player-card{
    position: relative;
    height: 60px;
    width: 90px;
}
.player-card img{
     height: 60px;
    width: 40px;
    position: absolute;
}
.player-card img:nth-child(2){
    left: 20px;
}
.player-card img:nth-child(3){
    left: 40px;
}
.player-info{
    height: 30px;
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
}
.player-head{
    height: 30px;
    width: 30px;
}
.player-head img{
    height: 30px;
    width: 30px;
}
.player-name{
     text-align: left;
    font-size: 12px;
    color: #E7E3F6;
    line-height: 16px;
    margin-left: 3px;
}
.bets-title{
    color: #fff;
    font-size: 12px;
    padding: 5px;
    background-color: rgba(255, 255, 255, .1)
}
.row{
    display: flex;
    flex-wrap: nowrap;
}
.td{
    color: #ECE4F1;
    border:1px solid #7D4ED1;
    width: 33%;
    font-size: 12px;
    line-height: 20px;
    
}
.row .td:nth-child(2){
    border-left: none;
    border-right: none;
}
.col-name .td{
    line-height: 26px;
    border: 1px solid #BACACD;
    background-color: #01C11C;
}
.col-type .td{
    line-height: 24px;
}
.row-chip .td{
    line-height: 30px;
}
.user-chip .td{
    line-height: 40px;
}
.user-td{
    color: #DCBD3A;
}
.bets-btn{
    font-size: 14px;
    color: #ECE4F1;
    margin-top: 10px;
    background-color: #BB096E;
}
.active{
    background-color: #BB096E;
    border-color: #D3B7DB;
}
</style>


