<template>
    <div>
        <mt-header title="三公大厅">
            <router-link to="/" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
        </mt-header>
        <div class="pageContent" id="sgHall">
            <div class="sgHall">
                <div class="box-container">
                    <div class="box"  @click="enterDesk(0)">
                        <div class="user">
                            <div class="head">
                                <img src="./../../../assets/图层15@2x.png">
                            </div>
                            <div class="info">
                                <div class="nickname"><p>新手区</p></div>
                                <div class="tag">倍率:{{ room.multiple[0] }}</div>
                            </div>
                        </div>
                        <div class="online">
                            <p>在线人数：</p>
                            <p>{{ room.online_setting[0] }}</p>
                        </div>
                        <div class="gold">
                            <p>金币：</p>
                            <p>{{ room.gold_min[0] }}-{{ room.gold_max[0] }}</p>
                        </div>
                    </div>
                    <div class="box" @click="enterDesk(1)">
                        <div class="user">
                            <div class="head">
                                <img src="./../../../assets/图层15@2x.png">
                            </div>
                            <div class="info">
                                <div class="nickname"><p>普通区</p></div>
                                <div class="tag">倍率:{{ room.multiple[1] }}</div>
                            </div>
                        </div>
                        <div class="online">
                            <p>在线人数：</p>
                            <p>{{ room.online_setting[1] }}</p>
                        </div>                        
                        <div class="gold">
                            <p>金币：</p>
                            <p>{{ room.gold_min[1] }}-{{ room.gold_max[1] }}</p>
                        </div>
                    </div>
                    <div class="box" @click="enterDesk(2)">
                        <div class="user">
                            <div class="head">
                                <img src="./../../../assets/图层15@2x.png">
                            </div>
                            <div class="info">
                                <div class="nickname"><p>高手区</p></div>
                                <div class="tag">倍率:{{ room.multiple[2] }}</div>
                            </div>
                        </div>
                        <div class="online">
                            <p>在线人数：</p>
                            <p>{{ room.online_setting[2] }}</p>
                        </div>                        
                        <div class="gold">
                            <p>金币：</p>
                            <p>{{ room.gold_min[2] }}-{{ room.gold_max[2] }}</p>
                        </div>
                    </div>
                    <div class="box" @click="enterDesk(3)">
                        <div class="user">
                            <div class="head">
                                <img src="./../../../assets/图层15@2x.png">
                            </div>
                            <div class="info">
                                <div class="nickname"><p>大神区</p></div>
                                <div class="tag">倍率:{{ room.multiple[3] }}</div>
                            </div>
                        </div>
                        <div class="online">
                            <p>在线人数：</p>
                            <p>{{ room.online_setting[3] }}</p>
                        </div>                        
                        <div class="gold">
                            <p>金币: </p>
                            <p>{{ room.gold_min[3] }}-{{ room.gold_max[2] }}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
console.log("GAME_SANGONG_SGHALL_VUE");
import root from "@/config/root.js";
    export default {
        data(){
            return {
                room:this.SANGONG.room,
            }
        },
        methods:{
            enterDesk: function(desk){
                console.log('client'+this.GLOBAL.userInfo.id+' click in '+desk);
                //进入牌桌前的检测
                this.SanGongEnter(desk);
            }
        },
        mounted: function(){
            // this.$store.state.isPageLogin = true;
            document.getElementById('sgHall').style.height = (window.screen.height-40) + 'px';
        },
        beforeCreate: function() {
            console.log("createSocket");
            if(!this.SANGONG.socketHand){
                 this.SANGONG.socketHand = new WebSocket("ws://127.0.0.1:2300");
            }
            //  this.SANGONG.socketHand=new WebSocket("ws://127.0.0.1:2300");
            console.log(this.SANGONG.socketHand);
            var that=this;
            var data={type:"enterRoom"};
            data = JSON.stringify(data);
            //当connection 处于open状态时进行传送数据
            that.SANGONG.socketHand.addEventListener('open',function(){
                that.SANGONG.socketHand.send(data);
            })
            that.SANGONG.socketHand.onmessage = function(e){
                var e = JSON.parse(e.data);
                console.log(e);
                console.log(that.SANGONG.room);
                switch(e.code){
                    //999游戏正在结算
                    case 999:
                    console.log("-----游戏正在结算-----");
                    break;
                    //1初始化牌桌信息
                    case 1:
                    console.log(that.SANGONG.desk);
                    that.SANGONG.desk=e.desk;//桌面信息
                    console.log(that.SANGONG.desk);
                    that.$router.push('/sangong');
                    break;
                    //初始化房间信息
                    case 2:
                    that.SANGONG.room = e.roomInfo;
                    that.room = that.SANGONG.room;
                    default:
                    break;
                }
            }
            document.getElementsByTagName("body")[0].className  ="mg0";
            document.getElementsByTagName("html")[0].className="mg0";
        },
        beforeDestroy: function() {
            // this.SANGONG.socketHand.close();//关闭页面关闭socket
            document.getElementsByTagName("html")[0].removeAttribute("class","mg0");
            document.body.removeAttribute("class","mg0");
        },
    }
    // ws.onmessage = function(e){
    //     console.log(e.data);
    // }   
</script>

<style scoped>
#sgHall{
    background: url('./../../../assets/色彩平衡1@2x.png') no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    overflow-y: auto;
}
.sgHall{
    padding: 20px;
}
.mint-header{
    background-color: #222222!important;
}
.box-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: -4%;
}
.box{
    display: block;
    width: 95%;
    border: 2px solid #fff;
    border-radius: 10px;
    height: 150px;
    margin-left: 4%;
    margin-top: 10px;
    overflow: hidden;
}
p{float: left;}
.user{
    display: flex;
    flex-wrap: nowrap;
    margin: 10px 5px 0;
}
.head{
    width: 50px;
    height: 50px;
    min-width: 50px;
}
.head img{
    width: 100%;
    height: 100%;
}
.info{
    margin: 5px;
    line-height: 20px;
    color: #fff;
    text-align: left;
    width: 100%;
}
.nickname{
    font-size: 12px;
}
.nickname p{
    overflow: hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 1;
-webkit-box-orient: vertical;
}
.tag{
    font-size: 16px;
}
.online{
    padding: 10px 15px;
    color: #F8E00D;
    text-align: left;
    line-height: 24px;
    float: left;
}
.gold{
    padding: 10px 15px;
    color: #F8E00D;
    text-align: left;
    line-height: 24px;
    float: left;
}
.gold p:first-child{
    font-size: 14px
}
.gold p:last-child{
    font-size: 18px;
}
.box-container .box:nth-child(6n+1){
    background-color: #7E07A9;
}
.box-container .box:nth-child(6n+2){
    background-color: #462B83;
}
.box-container .box:nth-child(6n+3){
    background-color: #3C52A0;
}
.box-container .box:nth-child(6n+4){
    background-color: #2C3E82;
}
.box-container .box:nth-child(6n+5){
    background-color: #A68E00;
}
.box-container .box:nth-child(6n+6){
    background-color: #4212AF;
}
</style>


